.animated-border {
  /* static borders: https://kovart.github.io/dashed-border-generator/ */
  background-image: repeating-linear-gradient(
      -45deg,
      transparent,
      transparent 24px,
      #e6db74 24px,
      #e6db74 48px
    );
  background-size: calc(100% + 68px);
}

/* reducing motion is probably unnecessary but i feel clever doing it */

@media screen and (prefers-reduced-motion: no-preference) {
  .animated-border {
    -webkit-animation: border-animation 3s linear infinite;
    animation: border-animation 3s linear infinite;
  }
}

@-webkit-keyframes border-animation {
  from {
    /* why 68? i don't know! seems to work well on most browsers */
    background-position: -68px;
  }

  to {
    background-position: 0;
  }
}

@keyframes border-animation {
  from {
    /* why 68? i don't know! seems to work well on most browsers */
    background-position: -68px;
  }

  to {
    background-position: 0;
  }
}

